import React from 'react';
import axios from 'axios';
const paymentModal = props=>{
    const handleSubmit = (e)=>{
        e.preventDefault();
        const data = new FormData() 
        const card_no = document.getElementById('card_no');
        const month = document.getElementById('month');
        const year = document.getElementById('year');
        const cvv = document.getElementById('cvv');
        const errorMessage = document.getElementById('error-message');
        if(card_no.value.length !== 14){
            errorMessage.innerHTML = '=> card no should be 14'
            card_no.focus();
        }
        else if(year.value.length !== 4 | parseInt(year.value) < 2021){
            errorMessage.innerHTML = '=> expired year'
            year.focus();
        }
        else if(month.value.length !== 2 | parseInt(month.value) > 12){
            errorMessage.innerHTML = '=>month should not exeed 12'
            month.focus();
        }
        else if(cvv.value.length !== 3){
            errorMessage.innerHTML = '=>cvv should have 3 digits'
            cvv.focus();
        }
        else{
            data.append('card_no',card_no.value)
            data.append('month',month.value)
            data.append('year',year.value)
            data.append('cvv',cvv.value)
            axios.put(`/customer/bills/`,data,{ headers: {Authorization: 'Bearer ' + window.localStorage.getItem('token')}})
            .then((resp)=>{
                errorMessage.innerHTML = '=>Succesfull redirecting...'
                window.setTimeout(()=>{
                    props.fallbackFunck(resp.data)
                    props.closeModal()
                },2000)
            })
            .catch((errors)=>{
                console.log(errors)
            })

        }

        // data.append ('price',product.price)
        // data.append ('size',product.size)
        // data.append ('suffix',product.suffix)
    }
    return (
        <div>
            <div className='backdrop' onClick={()=>props.closeModal()}></div>
            <div className='modal-content small'>
                <span className='back-btn' onClick={()=>props.closeModal()}>
                    x
                </span>
                <div className='modal-data'>
                    <div className='modal-form form-content'>
                    <p id='error-message' style={{'color':'#903D01','textAlign':'center','paddingTop':'10px'}}></p>
                        <form onSubmit={(e)=>handleSubmit(e)}>
                            <input type='text' placeholder='Card No' defaultValue={props.data.card_no} id='card_no' required/>
                            <input type='text' placeholder='Month' defaultValue={props.data.month} id='month' required/>
                            <input type='text' placeholder='Year' defaultValue={props.data.year} id='year' required/>
                            <input type='text' placeholder='CVV' defaultValue={props.data.cvv} id='cvv' required/>
                            <input type='submit' value='Update' className='submit-btn'/>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    )
}
export default paymentModal;