import React from 'react';
import axios from 'axios';
const Salary = ()=>{
    const generateBills = ()=>{
        const response = document.getElementById('response-button')
        response.innerHTML = 'Loading..'
        axios.post('/accountant/generatesalaries/',null,{ headers: {Authorization: 'Bearer ' + window.localStorage.getItem('token')}})
        .then((resp)=>{
            response.innerHTML = 'Generated Succesfully'
            response.classList.add('disabled')
        })
        .catch((error)=>{
            console.log(error)
        })
    }
    return (
        <div style={{'backgroundColor':'#fff','borderRadius':'14px','padding':'20px'}}>
            <h2 style={{'textAlign':'center','marginBottom':'20px'}}>Generate Salary to Paperboys</h2>
            <center><button className='add-btn' onClick={()=>generateBills()} id='response-button'>Generate</button></center>
            {/* <p style={{'textAlign':'center'}} id='response'></p> */}
        </div>
    )
}
export default Salary;