import React,{useState,useEffect} from 'react';
import axios from 'axios';
const BillModal = props=>{
    const [paymentDetails,setPaymentDetails] = useState('')
    useEffect(()=>{
        axios.get('/customer/payment-details/',{ headers: {Authorization: 'Bearer ' + window.localStorage.getItem('token')}})
        .then(resp=>{
            setPaymentDetails(resp.data)
        })
        .catch(errors=>{
            console.log(errors)
        })
    },[])
    const payBill = ()=>{
        axios.post(`/customer/pay-bill/${props.id}`,null,{ headers: {Authorization: 'Bearer ' + window.localStorage.getItem('token')}})
        .then(resp=>{
            props.redirectFunc()
            props.closeModal()
        })
        .catch(errors=>{
            console.log(errors)
        })
        // console.log(history)
        // history.push('dashboard/customer')
    }
    const renderData = ()=>{
        return (
            <div className='card-details'>
                <h4>Card Details</h4>
                <div className='divider'></div>
                <span><span style={{'color':'grey'}}>Card No : </span>{paymentDetails.card_no}</span>
                <span><span style={{'color':'grey'}}>MM/YY : </span>{paymentDetails.month} / {paymentDetails.year}</span>
                <span><span style={{'color':'grey'}}>CVV : </span>{paymentDetails.cvv}</span>
                <div className='divider'></div>
                <button onClick={()=>payBill()}>Pay</button>
            </div>
        )
    }
    return (
        <div>
            <div className='backdrop' onClick={()=>props.closeModal()}></div>
            <div className='modal-content small'>
                <span className='back-btn' onClick={()=>props.closeModal()}>
                    x
                </span>
                <div className='modal-data'>
                    {paymentDetails === '' ? <div>Loading...</div> : renderData()}
                </div>
            </div>
        </div>
    )
}
export default BillModal;