import React from 'react';
import axios from 'axios';
const AddModal =(props)=>{
    const handleSubmit = (e)=>{
        e.preventDefault()
        const email = document.getElementById('email')
        const password = document.getElementById('password')
        const name = document.getElementById('name')
        const pincode = document.getElementById('pincode')
        const salary = document.getElementById('salary')
        const card_no = document.getElementById('card_no')
        const month = document.getElementById('month')
        const year = document.getElementById('year')
        const cvv = document.getElementById('cvv')
        const data = new FormData()
        data.append('email',email.value)
        data.append('password',password.value)
        data.append('name',name.value)
        data.append('pincode',pincode.value)
        data.append('salary',salary.value)
        data.append('card_no',card_no.value)
        data.append('month',month.value)
        data.append('cvv',cvv.value)
        data.append('year',year.value)
        axios.post('/manager/paperboys/',data,{ headers: {Authorization: 'Bearer ' + window.localStorage.getItem('token')}})
        .then((resp)=>{
            props.successfullFunc()
        })
        .catch((errors)=>{
            console.log(errors)
        })
    }
    return (
        <div>
        <div className='backdrop' onClick={()=>props.closeModal()}></div>
        <div className='modal-content small'>
            <span className='back-btn' onClick={()=>props.closeModal()}>
                x
            </span>
            <div className='modal-data'>
                <div className='modal-form form-content'>
                <p id='error-message' style={{'color':'#903D01','textAlign':'center','paddingTop':'10px'}}></p>
                    <form onSubmit={(e)=>handleSubmit(e)}>
                        <input type='email' placeholder='email' id='email' required/>
                        <input type='password' placeholder='password' id='password' required/>
                        <input type='text' placeholder='Paperboy Name' id='name' required/>
                        <input type='text' placeholder='pincode' id='pincode' required/>
                        <input type='text' placeholder='salary' id='salary' required/>
                        <input type='text' placeholder='card_no' id='card_no' required/>
                        <input type='text' placeholder='month' id='month' required/>
                        <input type='text' placeholder='year' id='year' required/>
                        <input type='text' placeholder='cvv' id='cvv' required/>
                        <input type='submit' value='Update' className='submit-btn'/>
                    </form>
                </div>
            </div>
        </div>
    </div>
    )
}
export default AddModal;