import React,{useEffect,useState} from 'react';
import '../../styles.css';
import axios from 'axios';
import Confirm from './confirm';
import AddModal from './addModal'
const Subscription = props =>{
    const [data,setData]=useState('')
    const [modal,setModal] = useState(false)
    const [addModal,setAddModal] = useState(false)
    const [id,setId] = useState(0)
    useEffect(()=>{
        axios.get('/customer/subscription/',{ headers: {Authorization: 'Bearer ' + window.localStorage.getItem('token')}})
            .then(resp=>{
                setData(resp.data)
            })
            .catch(errors=>{
                console.log(errors)
            })
        },[])
        const succefullDelete = ()=>{

            axios.get('/customer/subscription/',{ headers: {Authorization: 'Bearer ' + window.localStorage.getItem('token')}})
            .then(resp=>{
                setData(resp.data)
            })
            .catch(errors=>{
                console.log(errors)
            })
            setModal(false)
        }
        const succefullAdd = ()=>{
            axios.get('/customer/subscription/',{ headers: {Authorization: 'Bearer ' + window.localStorage.getItem('token')}})
            .then(resp=>{
                setData(resp.data)
            })
            .catch(errors=>{
                console.log(errors)
            })
            setAddModal(false)
        }
        const closeModal = ()=>{
            setModal(false)
        }
        const showModal = ()=>{
            return <Confirm closeModal ={closeModal} successfullFunc = {succefullDelete} deleteURL={`/customer/subscription/delete/${id}`}/>
        }
        const closeAddModal = ()=>{
            setAddModal(false)
        }
        const showAddModal = ()=>{
            return <AddModal closeModal ={closeAddModal} successfullFunc = {succefullAdd} />
        }
        const renderData = ()=>{
            return (
                <span>
                    <h2 style={{'textAlign':'center','marginBottom':'10px'}}>Your Subscription</h2>
                    <center>- Amount &#8377; {data.amount}</center>
                    <center><button className='add-btn' onClick={()=>{
                        setAddModal(true)
                    }}>Add</button></center>
                    <div className='subscription-data'>
                        {data.papers.map(dataItem=>{
                        return (
                            <div key={dataItem.id} className='image-container'>
                                <img src={`${dataItem.image}`} alt='' />
                                <div className='imge-data'>
                                    <span>
                                        <span className='title'>{dataItem.title}</span>
                                        <span className='vertical-divider'>|</span>
                                        <span className='price'>&#8377; {dataItem.price}</span>
                                    </span>
                                    <button onClick={()=>{
                                        setId(dataItem.id);
                                        setModal(true);
                                    }}>Delete</button>
                                </div>
                            </div>
                        )
                         })}
                    </div>
                </span>
            )
        }
    return (
        <div>
            {data === '' ?<div>Loading</div> : renderData()}
            {modal ? showModal():null}
            {addModal ? showAddModal():null}
        </div>
    )
}

export default Subscription;