import Axios from 'axios';
import React,{useState,useEffect} from 'react';
import axios from 'axios';
import AddModal from './addpaper'
import Confirm from './paperConfirm'
const NewsPaper = ()=>{
    const [data,setData] = useState('')
    const [modal,setModal] = useState(false)
    const [id,setId] = useState(0)
    const [confirm,setConfirm] = useState(false)
    useEffect(()=>{
        axios.get('/adminapp/newspapers/',{ headers: {Authorization: 'Bearer ' + window.localStorage.getItem('token')}})
        .then(resp=>{
            // console.log(resp.data)
            setData(resp.data)
        })
        .catch(errors=>{
            console.log(errors)
        })
    },[])
    const successfullPay = ()=>{
        axios.get('/adminapp/newspapers/',{ headers: {Authorization: 'Bearer ' + window.localStorage.getItem('token')}})
        .then(resp=>{
            setData(resp.data)
        })
        .catch(errors=>{
            console.log(errors)
        })
        setModal(false)
    }
    const successfullDelete = ()=>{
        axios.get('/adminapp/newspapers/',{ headers: {Authorization: 'Bearer ' + window.localStorage.getItem('token')}})
        .then(resp=>{
            setData(resp.data)
        })
        .catch(errors=>{
            console.log(errors)
        })
        setConfirm(false)
    }
    const closeConfirm = ()=>{
        setConfirm(false)
    }
    const closeModal = ()=>{
        setModal(false)
    }
    const showConfirm = ()=>{
        return <Confirm closeModal ={closeConfirm} id={id} successfullFunc={successfullDelete} />
    }
    const showModal = ()=>{
        return <AddModal closeModal ={closeModal} redirectFunc={successfullPay}/>
    }
    const renderData = ()=>{
        return data.map((dataItem)=>{
            return (
                <div key={dataItem.id} className='image-container'>
                    <img src={`${dataItem.image}`} alt='' />
                    <div className='imge-data'>
                        <span>
                            <span className='title'>{dataItem.title}</span>
                            <span className='vertical-divider'>|</span>
                            <span className='price'>&#8377; {dataItem.price}</span>
                        </span>
                        <button onClick={()=>{
                            setId(dataItem.id);
                            setConfirm(true);
                        }} >Delete</button>
                    </div>
                </div>
            )
             })
        }
    return (
        <div>
            <h2 style={{'textAlign':'center'}}>News Papers</h2>
            <center><button className='add-btn' style={{'margin':'20px 0'}} onClick={()=>setModal(true)}>Add</button></center>
            <div className='subscription-data'>
                {data === '' ? <div>Loading...</div>:renderData()}
            </div>
            {modal === false ? null: showModal()}
            {confirm === false ? null : showConfirm()}
        </div>
    )
}
export default NewsPaper;