import Axios from 'axios';
import React,{useState,useEffect} from 'react';
import axios from 'axios';
import AddModal from './Add'
import Confirm from './confirm'
const PaperBoy = ()=>{
    const [data,setData] = useState('')
    const [modal,setModal] =useState(false)
    const [confirm,setConfirm] = useState(false)
    const [id,setId] = useState(0)
    useEffect(()=>{
        axios.get('/manager/paperboys/',{ headers: {Authorization: 'Bearer ' + window.localStorage.getItem('token')}})
        .then(resp=>{
            console.log(resp.data)
            setData(resp.data)
        })
        .catch(errors =>{
            console.log(errors)
        })
    },[])
    const succesufullAdd = ()=>{
        axios.get('/manager/paperboys/',{ headers: {Authorization: 'Bearer ' + window.localStorage.getItem('token')}})
        .then(resp=>{
            setData(resp.data)
        })
        .catch(errors =>{
            console.log(errors)
        })
        setModal(false)
    }
    const succesufullDel = ()=>{
        axios.get('/manager/paperboys/',{ headers: {Authorization: 'Bearer ' + window.localStorage.getItem('token')}})
        .then(resp=>{
            setData(resp.data)
        })
        .catch(errors =>{
            console.log(errors)
        })
        setConfirm(false)
    }
    const closeModal = ()=>{
        setModal(false)
    }
    const closeConfirm = ()=>{
        setConfirm(false)
    }
    const showModal = ()=>{
        return <AddModal closeModal ={closeModal} successfullFunc = {succesufullAdd} />
    }
    const showConfirm = ()=>{
        return <Confirm closeModal = {closeConfirm} successfullFunc={succesufullDel} id={id}/>
    }
    const renderData = ()=>{
        return data.map((dataItem)=>{
            return (
                <tr>
                    <td>{dataItem.name}</td>
                    <td>{dataItem.salary}</td>
                    <td>{dataItem.pincode}</td>
                    {/* <td><button className='add-btn' style={{'fontSize':'.9rem','backgroundColor':'yellow','color':'black'}}>Update</button></td> */}
                    <td><button className='del-btn' onClick={()=>{
                        setId(dataItem.id);
                        setConfirm(true);
                    }}>Delete</button></td>
                </tr>
            )
        })
    }
    return (
        <div>
            <center><h2 style={{'marginBottom':'10px'}}>Paper Boys</h2></center>
            <center><button className='add-btn' onClick={()=>setModal(true)}>Add</button></center>
            <center><table>
                <tr>
                    <th>Name</th>
                    <th>Salary</th>
                    <th>Pincode</th>
                    <th></th>
                    <th></th>
                </tr>
                {data === '' ? <div>Loading...</div>:renderData()}
            </table></center>
            {modal ? showModal():null}
            {confirm ? showConfirm():null}
        </div>
    )
}
export default PaperBoy;