import React,{useState,useEffect} from 'react';
import '../../styles.css';
import Bills from './Bills';
import Daily from './daily';
import Payment from './payment';
import Settings from './settings';
import Subscription from './subscription';
import Image2 from '../../images/n4.jpg';
import CardIcon from '../../images/card.svg';
import BillIcon from '../../images/payment.svg';
import SettingsIcon from '../../images/settings.svg';
import DailyIcon from '../../images/daily.svg';
import SubscriptionIcon from '../../images/sub.svg';
import LogoutIcon from '../../images/logout.svg';
import axios from 'axios';
// import {useParams} from 'react-router-dom';
const Dashborad = props =>{
    // const {user_type} = useParams()
    const [category,setCategory] = useState('bills')
    const [info,setInfo] = useState('')
    useEffect(()=>{
        axios.get('/customer/info/',{ headers: {Authorization: 'Bearer ' + window.localStorage.getItem('token')}})
        .then((resp)=>{
            setInfo(resp.data)
        })
        .catch((errors)=>{
            console.log(errors)
        })
    },[])
    const renderCategory = () =>{
        if(category === 'bills'){
            return <Bills />
        }
        else if(category === 'daily'){
            return <Daily />
        }
        else if(category === 'settings'){
            return <Settings info = {info}/>
        }
        else if(category === 'subscription'){
            return <Subscription />
        }
        else if (category === 'payment'){
            return <Payment />
        }
    }
    const changeCategory = (cat,e) =>{
        const allDivs = document.getElementsByClassName('dashboard-sidenav-links-icon')
        for (var i = 0; i < allDivs.length; i++) {
            allDivs[i].classList.remove('active');
        }
        e.currentTarget.classList.add('active')
        setCategory(cat)
    }
    return (
        <div className='dashborad-header' style={{'backgroundImage':`url(${Image2})`,'backgroundPosition':'center','backgroundSize':'cover'}}>
            <div className='dashboard-container'>
                <div className='dashboard-sidenav glass'>
                    <div className='dashboard-sidenav-header'>
                        <p className='username'>{info.name}</p>
                        <p className='branch'>{info.branch}</p>
                        <div className='divider'></div>
                    </div>
                    <div className='dashboard-sidenav-links'>
                        <div className='dashboard-sidenav-links-icon active' onClick={(e)=>changeCategory('bills',e)}>
                            <span className='dashboard-sidenav-links-icon-text'>Monthly Bills</span>
                            <img src={BillIcon} className='dashboard-sidenav-links-icon-icon special-icon' alt=''/>
                        </div>
                        <div className='dashboard-sidenav-links-icon' onClick={(e)=>changeCategory('payment',e)}>
                            <span className='dashboard-sidenav-links-icon-text'>Payments</span>
                            <img src={CardIcon} className='dashboard-sidenav-links-icon-icon' alt=''/>
                        </div>
                        <div className='dashboard-sidenav-links-icon' onClick={(e)=>changeCategory('settings',e)}>
                            <span className='dashboard-sidenav-links-icon-text'>Settings</span>
                            <img src={SettingsIcon} className='dashboard-sidenav-links-icon-icon'alt=''/>
                        </div>
                        <div className='dashboard-sidenav-links-icon' onClick={(e)=>changeCategory('subscription',e)}>
                            <span className='dashboard-sidenav-links-icon-text'>Subscriptions</span>
                            <img src={SubscriptionIcon} className='dashboard-sidenav-links-icon-icon'alt=''/>
                        </div>
                        <div className='dashboard-sidenav-links-icon' onClick={(e)=>changeCategory('daily',e)}>
                            <span className='dashboard-sidenav-links-icon-text'>Daily Report</span>
                            <img src={DailyIcon} className='dashboard-sidenav-links-icon-icon'alt=''/>
                        </div>
                    </div>
                    <div className='dashborad-footer'>
                        <div className='divider'></div>
                        <center><img src={LogoutIcon} alt='' style={{'cursor':'pointer'}} onClick={()=>{
                                    window.localStorage.removeItem('token')
                                    window.location = '/'
                    }}/></center>
                    </div>
                </div>
                <div className='dashboard-main glass'>
                    {renderCategory()}
                </div>
            </div>
        </div>
    )
}

export default Dashborad;