import React,{useState,useEffect} from 'react';
import axios from 'axios';
import '../../styles.css';
import AddModal from './addbranch'
import Confirm from './confirm'
const Branch = ()=>{
    const [data,setData] = useState('')
    const [modal,setModal] = useState(false)
    const [id,setId] = useState(0)
    const [confirm,setConfirm] = useState(false)
    useEffect(()=>{
        axios.get('/adminapp/branches/',{ headers: {Authorization: 'Bearer ' + window.localStorage.getItem('token')}})
        .then(resp=>{
            // console.log(resp.data)
            setData(resp.data)
        })
        .catch(errors=>{
            console.log(errors)
        })
    },[])
    const successfullPay = ()=>{
        axios.get('/adminapp/branches/',{ headers: {Authorization: 'Bearer ' + window.localStorage.getItem('token')}})
        .then(resp=>{
            setData(resp.data)
        })
        .catch(errors=>{
            console.log(errors)
        })
        setModal(false)
    }
    const successfullDelete = ()=>{
        axios.get('/adminapp/branches/',{ headers: {Authorization: 'Bearer ' + window.localStorage.getItem('token')}})
        .then(resp=>{
            setData(resp.data)
        })
        .catch(errors=>{
            console.log(errors)
        })
        setConfirm(false)
    }
    const closeConfirm = ()=>{
        setConfirm(false)
    }
    const closeModal = ()=>{
        setModal(false)
    }
    const showConfirm = ()=>{
        return <Confirm closeModal ={closeConfirm} id={id} successfullFunc={successfullDelete} />
    }
    const showModal = ()=>{
        return <AddModal closeModal ={closeModal} redirectFunc={successfullPay}/>
    }
    const renderData = ()=>{
        return data.map((dataItem)=>{
            // console.log(dataItem)
            return (
                <div key={dataItem.id} className='branches'>
                    <center><p><span style={{'color':'grey'}}> Branch Name: </span> {dataItem.branch.name}</p>
                    <p><span style={{'color':'grey'}}> Manger Name: </span> {dataItem.name}</p>
                    <button className='del-btn' styles={{'marginTop':'5px'}} onClick={()=>{
                        setId(dataItem.id);
                        setConfirm(true);
                    }}>Delete</button></center>
                </div>
            )
        })
    }
    return (
        <div>
            <h2 style={{'textAlign':'center'}}>Banches</h2>
            <center><button className='add-btn' style={{'margin':'20px 0'}} onClick={()=>setModal(true)}>Add</button></center>
            {data === '' ? <div>Loading...</div>:renderData()}
            {modal === false ? null:showModal()}
            {confirm === false ? null :showConfirm()}
        </div>
    )
}
export default Branch;