import React,{useState} from 'react';
import axios from 'axios';
const AddModal =(props)=>{
    const [image,setImage] =useState('')
    const handleSubmit = (e)=>{
        e.preventDefault()
        const title = document.getElementById('name')
        const price = document.getElementById('price')
        const data = new FormData()
        data.append('title', title.value)
        data.append('price',price.value)
        data.append('image',image)
        axios.post('/adminapp/newspapers/',data,{ headers: {Authorization: 'Bearer ' + window.localStorage.getItem('token')}})
        .then((resp)=>{
            props.redirectFunc()
        })
        .catch((errors)=>{
            console.log(errors)
        })
    }
    return (
        <div>
        <div className='backdrop' onClick={()=>props.closeModal()}></div>
        <div className='modal-content small'>
            <span className='back-btn' onClick={()=>props.closeModal()}>
                x
            </span>
            <div className='modal-data'>
                <div className='modal-form form-content'>
                <p id='error-message' style={{'color':'#903D01','textAlign':'center','paddingTop':'10px'}}></p>
                    <form onSubmit={(e)=>handleSubmit(e)}>
                        <input type='text' placeholder='Paper Name' id='name' required/>
                        <input type='text' placeholder='Price' id='price' required/>
                        <input type='file' id="img" name="img" accept="image/*" onChange={(e)=>setImage(e.target.files[0])} required/>
                        <input type='submit' value='Add' className='submit-btn'/>
                    </form>
                </div>
            </div>
        </div>
    </div>
    )
}
export default AddModal;