import React,{useState} from 'react';
import '../../styles.css';
import Branch from './branch';
import NewsPaper from './newsPapers';
import Image2 from '../../images/n4.jpg';
import SettingsIcon from '../../images/settings.svg';
import DailyIcon from '../../images/daily.svg';
import LogoutIcon from '../../images/logout.svg';
// import {useParams} from 'react-router-dom';
const Dashborad = props =>{
    // const {user_type} = useParams()
    const [category,setCategory] = useState('branch')
    const renderCategory = () =>{
        if(category === 'branch'){
            return <Branch />
        }
        else if(category === 'papers'){
            return <NewsPaper />
        }
    }
    const changeCategory = (cat,e) =>{
        const allDivs = document.getElementsByClassName('dashboard-sidenav-links-icon')
        for (var i = 0; i < allDivs.length; i++) {
            allDivs[i].classList.remove('active');
        }
        e.currentTarget.classList.add('active')
        setCategory(cat)
    }
    return (
        <div className='dashborad-header' style={{'backgroundImage':`url(${Image2})`,'backgroundPosition':'center','backgroundSize':'cover'}}>
            <div className='dashboard-container'>
                <div className='dashboard-sidenav glass'>
                    <div className='dashboard-sidenav-header'>
                        <p className='username'>Admin</p>
                        <div className='divider'></div>
                    </div>
                    <div className='dashboard-sidenav-links'>
                        <div className='dashboard-sidenav-links-icon active' onClick={(e)=>changeCategory('branch',e)}>
                            <span className='dashboard-sidenav-links-icon-text'>Branch</span>
                            <img src={SettingsIcon} className='dashboard-sidenav-links-icon-icon special-icon' alt=''/>
                        </div>
                        <div className='dashboard-sidenav-links-icon' onClick={(e)=>changeCategory('papers',e)}>
                            <span className='dashboard-sidenav-links-icon-text'>News Papers</span>
                            <img src={DailyIcon} className='dashboard-sidenav-links-icon-icon' alt=''/>
                        </div>
                    </div>
                    <div className='dashborad-footer'>
                        <div className='divider'></div>
                        <center><img src={LogoutIcon} alt='' style={{'cursor':'pointer'}} onClick={()=>{
                                    window.localStorage.removeItem('token')
                                    window.location = '/'
                    }}/></center>
                    </div>
                </div>
                <div className='dashboard-main glass'>
                    {renderCategory()}
                </div>
            </div>
        </div>
    )
}

export default Dashborad;