import React,{useEffect,useState} from 'react';
import './styles.css';
import axios from 'axios';
const Work = ()=>{
    const [data,setData] = useState('')
    useEffect(()=>{
        axios.get('/paperboy/work/',{ headers: {Authorization: 'Bearer ' + window.localStorage.getItem('token')}})
        .then((resp)=>{
            setData(resp.data)
        })
        .catch((error)=>{
            console.log(error)
        })
    },[])
    const renderData = ()=>{
        return data.map((dataItem)=>{
            if(dataItem.subscription.papers.length === 0){
                return null
            }
            return (
                <tr>
                    <td>{dataItem.name}</td>
                    <td>
                        {dataItem.subscription.papers.map((paper)=><span>{paper.title} , </span>)}
                    </td>
                </tr>
            )
        })
    }
    return (
        <div>
            <cetner><table>
                <tr>
                    <th>Name</th>
                    <th>Papers</th>
                </tr>
                {data === '' ? <div>Loading..</div>:renderData()}
            </table></cetner>
        </div>
    )
}
export default Work;