import React,{useEffect,useState} from 'react';
import '../../styles.css';
import PaymentModal from './paymentModal';
import axios from 'axios';
const Payment = props =>{
    const [paymentDetails,setPaymentDetails] = useState('')
    const [modal,setModal] = useState(false)
    useEffect(()=>{
        axios.get('/customer/payment-details/',{ headers: {Authorization: 'Bearer ' + window.localStorage.getItem('token')}})
        .then(resp=>{
            setPaymentDetails(resp.data)
        })
        .catch(errors=>{
            console.log(errors)
        })
    },[])
    const closeModal = ()=>{
        setModal(false)
    }
    const fallbackFunck = (data)=>{
        setPaymentDetails(data)
    }
    const showModal = ()=>{
        return <PaymentModal closeModal ={closeModal} data={paymentDetails} fallbackFunck={fallbackFunck}/>
    }
    return(
        <div className='payment-container'>
            <div className='card-details'>
                <h4>Card Details</h4>
                <div className='divider'></div>
                <span><span style={{'color':'grey'}}>Card No : </span>{paymentDetails.card_no}</span>
                <span><span style={{'color':'grey'}}>MM/YY : </span>{paymentDetails.month} / {paymentDetails.year}</span>
                <span><span style={{'color':'grey'}}>CVV : </span>{paymentDetails.cvv}</span>
                <div className='divider'></div>
                <button onClick={()=>setModal(true)}>Update</button>
                {modal ? showModal():null }
            </div>
        </div>
    )
}

export default Payment;