import React from 'react';

const BasicForm = (props) =>{
    const handleFormData = ()=>{
        const email     = document.getElementById('email');
        const password  = document.getElementById('password');
        const name      = document.getElementById('name');
        if (email.value === ''){
            email.focus();
        }
        else if (password.value === ''){
            password.focus();
        }
        else if(name.value === '' ){
            name.focus();
        }
        else{
            const data ={
                email : email.value,
                password : password.value,
                name : name.value
            }
            props.receiveData(data)
        }
    }
    return (
        <div className='form-content'>
            <form>
                <input type='email' placeholder='Email Adress' id='email'/>
                <input type='password' placeholder='Password' id='password'/>
                <input type='text' placeholder='Your Name' id='name'/>
            </form>
            <div className='form-content-buttons'>
                <button className='disable'><span>&#8592;</span> Previous </button>
                <button onClick={()=>handleFormData()}> Next <span>&#8594;</span></button>
            </div>
        </div>
    )
}
export default BasicForm;