import React,{useState} from 'react';
import Image from '../images/n4.jpg';
import {useHistory} from 'react-router-dom'
import '../styles.css';
import BasicForm from './signupComponents/basicForm';
import AdressForm from './signupComponents/adressForm';
import PaymentForm from './signupComponents/paymentForm';
import axios from 'axios';
const Signup = props =>{
    const history = useHistory()
    const [data,setData]= useState({
        email:'',
        password:'',
        name:'',
        doorno:'',
        colony:'',
        landmark:'',
        village:'',
        mandal:'',
        pincode:'',
        card_no:'',
        cvv:'',
        month:'',
        year:'',

    })
    const [formno,setFormno] = useState(1)
    const receiveData = dataObject =>{
        setData({...data,...dataObject})
        const breadcum = document.getElementsByClassName('breadcum-text')
        breadcum[formno].classList.add('active')
        setFormno(formno + 1) 
    }
    const submitData = dataObject =>{
        const newData = {...data,...dataObject}
        axios.post('/accounts/create/customer',newData)
        .then(resp=>{
            history.push('/')
        })
        .catch(errors=>{
            console.log(errors)
        })
    }
    const renderForm = ()=>{
        if(formno === 1){
            return <BasicForm receiveData={receiveData}/>
        }
        else if(formno === 2){
            return <AdressForm receiveData={receiveData}/>
        }
        else{
            return <PaymentForm receiveData={submitData}/>
        }
    }
    // const handleSubmit = e =>{
    //     e.preventDefault();
    //     console.log('submitting...')
    // }
    return (
        <div style={{'backgroundImage':`url(${Image})`,'backgroundPosition':'center','backgroundSize':'cover','height':'100vh','width':'100vw'}}>
            <div className='form-container glass'>
                {/* <h1 style={{'textAlign':'center'}}>Signup Form</h1> */}
                <div className='breadcum'>
                    <span className='breadcum-text active'>Basic Details</span>
                    <span className='breadcum-icon'>&#8250;</span>
                    <span className='breadcum-text'>Adress</span>
                    <span className='breadcum-icon'>&#8250;</span>
                    <span className='breadcum-text'>Payment Info</span>
                </div>
                {renderForm()}
            </div>
        </div>
    )
}
export default Signup;