import React from 'react';

const PaymentForm = (props) =>{
    const handleFormData = ()=>{
        const card_no        = document.getElementById('cardno');
        const cvv        = document.getElementById('cvv');
        const month      = document.getElementById('month');
        const year       = document.getElementById('year');
        if (card_no.value === ''){
            card_no.focus();
        }
        else if (cvv.value === ''){
            cvv.focus();
        }
        else if(month.value === '' ){
            month.focus();
        }
        else if (year.value === ''){
            year.focus();
        }
        else{
            const data ={
                card_no:card_no.value,
                cvv:cvv.value,
                month:month.value,
                year:year.value
            }
            props.receiveData(data)
        }
    }
    return (
        <div className='form-content'>
            <form>
                <input type='text' placeholder='Card No' id='cardno'/>
                <input type='text' placeholder='cvv' id='cvv'/>
                <input type='text' placeholder='month' id='month'/>
                <input type='text' placeholder='year' id='year'/>
            </form>
            <div className='form-content-buttons'>
                <button className='disable'><span>&#8592;</span> Previous </button>
                <button onClick={()=>handleFormData()}> Next <span>&#8594;</span></button>
            </div>
        </div>
    )
}
export default PaymentForm;