import React from 'react';
import './styles.css';
import axios from 'axios';
const Confirm = (props) =>{
    const performDelete = ()=>{
        // const data = {'id':props.id}
        axios.delete(props.deleteURL,{ headers: {Authorization: 'Bearer ' + window.localStorage.getItem('token')}})
        .then(resp=>{
            // console.log(resp)
            props.successfullFunc()
            // window.location = '/customer'
        })
        .catch(errors=>{
            console.log(errors)
        })
    }
    return (
        <div>
            <div className='backdrop' onClick={()=>props.closeModal()}></div>
                <div className='modal-content small'>
                <span className='back-btn' onClick={()=>props.closeModal()}>
                    x
                </span>
                <center><div className='modal-data'>
                    {/* {paymentDetails === '' ? <div>Loading...</div> : renderData()} */}
                    Do you want to Delete?
                    <div className='button'>
                        <button className='del-btn' onClick={()=>performDelete()}>Delete</button>
                    </div>
                </div></center>
            </div>
        </div>
    )
}
export default Confirm;