import React from 'react';
import axios from 'axios';
const AddModal =(props)=>{
    const handleSubmit = (e)=>{
        e.preventDefault()
        const email = document.getElementById('email')
        const password = document.getElementById('password')
        const name = document.getElementById('name')
        const mname = document.getElementById('mname')
        const data = new FormData()
        data.append('email',email.value)
        data.append('password',password.value)
        data.append('name',name.value)
        data.append('mname',mname.value)
        axios.post('/adminapp/branches/',data,{ headers: {Authorization: 'Bearer ' + window.localStorage.getItem('token')}})
        .then((resp)=>{
            props.redirectFunc()
        })
        .catch((errors)=>{
            console.log(errors)
        })
    }
    return (
        <div>
        <div className='backdrop' onClick={()=>props.closeModal()}></div>
        <div className='modal-content small'>
            <span className='back-btn' onClick={()=>props.closeModal()}>
                x
            </span>
            <div className='modal-data'>
                <div className='modal-form form-content'>
                <p id='error-message' style={{'color':'#903D01','textAlign':'center','paddingTop':'10px'}}></p>
                    <form onSubmit={(e)=>handleSubmit(e)}>
                        <input type='email' placeholder='manager email' id='email' required/>
                        <input type='password' placeholder='manager password' id='password' required/>
                        <input type='text' placeholder='Branch Name' id='name' required/>
                        <input type='text' placeholder='Manager Name' id='mname' required/>
                        <input type='submit' value='Add' className='submit-btn'/>
                    </form>
                </div>
            </div>
        </div>
    </div>
    )
}
export default AddModal;