import React,{useState,useEffect} from 'react';
import './styles.css';
import PayIcon from '../../images/pay.svg';
import DataIcon from '../../images/date.svg';
import PaidIcon from '../../images/paid.svg'
import axios from 'axios';
import BillModal from './BIllModal'
const Bills = props =>{
    const [bills,setBills] = useState('')
    const [modal,setModal] = useState(false)
    const [id,setId] = useState(0)

    useEffect(()=>{
        axios.get('/customer/bills/',{ headers: {Authorization: 'Bearer ' + window.localStorage.getItem('token')}})
        .then(resp=>{
            setBills(resp.data)
        })
        .catch(errors=>{
            console.log(errors)
        })
    },[])
    const successfullPay = ()=>{
        // console.log(bills.pending)
        // const object = bills.pending.find(bill_obj=>bill_obj.id === id)
        // object.is_paid = true
        // const pending_array = bills.pending.filter(bill_obj => bill_obj === id)
        // const paid_array = bills.paid
        // paid_array.push(object)
        // setBills({'pending':pending_array,'paid':paid_array})
        axios.get('/customer/bills/',{ headers: {Authorization: 'Bearer ' + window.localStorage.getItem('token')}})
        .then(resp=>{
            setBills(resp.data)
        })
        .catch(errors=>{
            console.log(errors)
        })
    }
    const closeModal = ()=>{
        setModal(false)
    }
    const showModal = ()=>{
        return <BillModal id={id} closeModal ={closeModal} redirectFunc={successfullPay}/>
    }
    const getFormattedDate = date=>{
        const date_array = date.split('-')
        return date_array[0]+ '-'+date_array[1]+'-'+date_array[2][0]+date_array[2][1];
    }
    const renderBills = ()=>{
        return (
            <div>
                {bills.pending.length === 0 ? null:
                <span><h2 className='bill-heading'>Pending Bills</h2>
                    <div className='box-container'>
                        {bills.pending.map(bill=>{
                            return (
                                <div className='bill-box' key={bill.id}>
                                    <p className='price'>$ {bill.amount} /-</p>
                                    {/* <p className='days-info'>30 days | 60 papers</p> */}
                                    <div className='date-info'>
                                        <span> <img src={DataIcon} alt=''/> <span>{getFormattedDate(bill.date)}</span></span>
                                        <span className='pay-link' onClick={()=>{
                                            setId(bill.id);
                                            setModal(true)
                                        }}><span style={{'color':'#fff'}}>Pay</span><img src={PayIcon} alt=''/></span>
                                    </div>
                                </div>
                            )
                        })}
                    </div>
                    <div className='divider' style={{'marginBottom':'10px'}}></div>
                </span>
                }
                {bills.paid.length === 0 ? null:
                    <span>
                        <h2 className='bill-heading'>Paid Bills</h2>
                        <div className='box-container'>
                            {bills.paid.map(bill=>{
                                return (
                                    <div className='bill-box' key={bill.id}>
                                        <p className='price'>$ {bill.amount} /-</p>
                                        {/* <p className='days-info'>30 days | 60 papers</p> */}
                                        <div className='date-info'>
                                            <span> <img src={DataIcon} alt=''/> <span>{getFormattedDate(bill.date)}</span></span>
                                            <span className='paid-link'>Paid<img src={PaidIcon} alt=''/></span>
                                        </div>
                                    </div>
                                )
                            })}
                        </div>
                    </span>
                }
            </div>
        )
    }
    return (
        <div>
            {bills === '' ? <div>Loading...</div>:renderBills()}
            {modal ? showModal():null }
        </div>
    )
}

export default Bills;