import React,{useEffect,useState} from 'react';
import '../../styles.css';
import axios from 'axios';
const Daily = props =>{
    const [data,setData] = useState('')
    useEffect(()=>{
        axios.get('/customer/reports/',{ headers: {Authorization: 'Bearer ' + window.localStorage.getItem('token')}})
        .then((resp)=>{
            setData(resp.data)
        })
        .catch((errors)=>{
            console.log(errors)
        })
    },[])
    const getFormattedDate = date=>{
        const date_array = date.split('-')
        return date_array[0]+ '-'+date_array[1]+'-'+date_array[2][0]+date_array[2][1];
    }
    const renderData = ()=>{
        return data.map((dataItem)=>{
            return (
                <tr key={dataItem.id}>
                    <td>amount:{dataItem.amount}</td>
                    <td>Date:{getFormattedDate(dataItem.date)}</td>
                    <td>No.of.Papers:{dataItem.no_of_papers}</td>
                </tr>
            )
        })
    }
    return (
        <div>
            <center>
                <table>
                    <tbody>
                        <tr>
                            <th>Amount</th>
                            <th>Date</th>
                            <th>No.of.papers</th>
                        </tr>
                        {data === '' ? <tr><td>Loading</td></tr> : renderData()}
                    </tbody>
                </table>
            </center>
        </div>
    )
}

export default Daily;