import React,{useEffect,useState} from 'react';
import './styles.css';
import axios from 'axios';
const Salary = ()=>{
    const [data,setData] = useState('')
    useEffect(()=>{
        axios.get('/paperboy/salaries/',{ headers: {Authorization: 'Bearer ' + window.localStorage.getItem('token')}})
        .then((resp)=>{
            setData(resp.data)
        })
        .catch((error)=>{
            console.log(error)
        })
    },[])
    const getFormattedDate = date=>{
        const date_array = date.split('-')
        return date_array[0]+ '-'+date_array[1]+'-'+date_array[2][0]+date_array[2][1];
    }
    const renderData = ()=>{
        return data.map((dataItem)=>{
            return (
                <tr key={dataItem.id}>
                    <td>{dataItem.amount}</td>
                    <td>{getFormattedDate(dataItem.date)}</td>
                </tr>
            )
        })
    }
    return (
        <div>
            <h2 style={{'textAlign':'center','marginBottom':'20px'}}>Salaries</h2>
            <center><table>
                <tr>
                    <th>Amount</th>
                    <th>Date</th>
                </tr>
                {data === '' ? <div>Loading...</div> : renderData()}
            </table></center>
        </div>
    )
}
export default Salary;