import React from 'react';
import '../styles.css';
import UserIcon from '../images/user.svg';
import PasswordIcon from '../images/password.svg';
import {Link} from 'react-router-dom'
// import {useHistory} from 'react-router-dom'
import axios from 'axios';
const Modal = props =>{
    const handleSubmit = e =>{
        // const history = useHistory()
        e.preventDefault()
        const emailInput = document.getElementById('email-input')
        const passwordInput = document.getElementById('email-password')
        if(emailInput.value === ''){
            emailInput.focus();
            const errorMessage = document.getElementById('error-message');
            errorMessage.innerHTML = '=> Email Required'
        }
        else if (passwordInput.value === ''){
            passwordInput.focus()
            const errorMessage = document.getElementById('error-message');
            errorMessage.innerHTML = '=> Password Required'
        }
        else{
            axios.post('/api/token/',{email:emailInput.value,password:passwordInput.value})
            .then(resp=>{
                window.localStorage.setItem('token', resp.data.access);
                window.location = '/'
                // history.push('/')
            })   
            .catch(errors =>{
                console.log(errors)
                const errorMessage = document.getElementById('error-message');
                errorMessage.innerHTML = '=> Invalid Username and password'
            })
        }
    }
    return (
        <div className='floating-login-form glass'>
            <p id='error-message' style={{'color':'#903D01','textAlign':'center','paddingTop':'10px'}}></p>
            {/* <p style={{'textAlign':'center','fontSize':'22px','letterSpacing':'1px'}}>Login</p> */}
            <form onSubmit={(e)=>handleSubmit(e)}>
                <div className='field-container'>
                    <img src={UserIcon} alt=''/>
                    <input type='email' placeholder='Email Adress' id='email-input'/>
                </div>
                <div className='field-container'>
                    <img src={PasswordIcon} alt=''/>
                    <input type='password' placeholder='Password' id='email-password'/>
                </div>
                <center>
                    <input type='submit' value='login' className='submit-btn'/>
                </center>
            </form>
            <Link to='/signup' className='link'>Sign Up <span className='arrow'>&#8594;</span></Link>
            <span className='back-btn' onClick={()=>props.closeFunc()}>
                x
            </span>
        </div>
    )
}
export default Modal;