import React, { useState,useEffect } from 'react'
import { Link } from 'react-router-dom'
import Modal from './Modal'
import '../styles.css'
import Image1 from '../images/n2.jpg';
import Image2 from '../images/n1.jpg';
import DashBoardIcon from '../images/dashboard.svg';
import LoginIcon from '../images/login.svg';
// import LogoutIcon from '../images/logout.svg'
import HeroImage from '../images/hero_image.png'
import PaymentIcon from '../images/payment.svg';
import SubscriptionIcon from '../images/subscription.svg';
import SecureIcon from '../images/secure.svg';
import CustomerIcon from '../images/customer.svg';
import UserIcon from '../images/user.svg';
import axios from 'axios';
const Home = props => {
    const [modal, setModal] = useState(false)
    const [userInfo,setUserInfo] = useState('')
    useEffect(()=>{
        if(window.localStorage.getItem('token')){
            axios.get('/accounts/get_info',{ headers: {Authorization: 'Bearer ' + window.localStorage.getItem('token')}})
            .then(resp=>{
                setUserInfo(resp.data)
            })
            .catch(errors =>{
                console.log(errors)
            })
        }
    },[])
    const closeModalFunc = () => {
        setModal(false)
    }
    const renderModalFunc = () => {
        return <Modal closeFunc={closeModalFunc} />
    }
    return (
        <span>
            <header className='header' style={{ 'backgroundImage': `url(${Image1})`, 'backgroundPosition': 'center', 'backgroundSize': 'cover' }}>
                <div className='glass header-container'>
                    <div className='container'>
                        <nav className='navbar'>
                            <span className='navbar-header'>News Wala</span>
                            <span className='navbar-link-container'>
                                <Link to={`/dashboard/${userInfo.user_type}`}><span className='navbar-link'>
                                    <img src={DashBoardIcon} className='icon' alt='' />
                                    <span >Dashboard</span>
                                </span></Link>
                                {userInfo === '' ? <span className='navbar-link special-nav-link button' onClick={() => setModal(true)}>
                                                        <img src={LoginIcon} className='icon' alt='' />
                                                        <span>login</span>
                                                    </span> 
                                : <span className='navbar-link special-nav-link button' onClick={()=>{
                                    window.localStorage.removeItem('token')
                                    window.location = '/'
                                }}>
                                    {/* <img src={LogoutIcon} className='icon' alt='' /> */}
                                    <span>Logout</span>
                                </span> }
                            </span>
                        </nav>
                        <div className='hero-container'>
                            <div className='hero-text'>
                                <p className='hero-text-header'>
                                    One Click
                                </p>
                                <p className='hero-text-content'>
                                    For all your newspaper related things to your hands
                                </p>
                                <p className='ghost-buttons'>
                                {userInfo === '' ?  <span className='button full-button'>
                                                        <img src={LoginIcon} className='icon' alt='' />
                                                        <span>login</span>
                                                    </span>
                                : <span className='button full-button'>
                                    <Link to='/dashboard' style={{'color':'#fff'}}>Dashboard</Link>
                                </span> }
                                    <span className='button half-button'>
                                        <span>Know More</span>
                                    </span>
                                </p>
                            </div>
                            <div className='hero-image'>
                                <img src={HeroImage} alt='' />
                            </div>
                        </div>
                    </div>

                </div>
            </header>
            {modal ? renderModalFunc() : null}
            <div className='section' style={{ 'backgroundImage': `url(${Image2})`, 'backgroundPosition': 'center', 'backgroundSize': 'cover' }}>
                <div className='container'>
                    <div className='features'>
                        <div className='feature-box glass'>
                            <img src={PaymentIcon} alt='' />
                            <span>Pay per Paper Monthly Bills</span>
                        </div>
                        <div className='feature-box glass'>
                            <img src={SubscriptionIcon} alt='' />
                            <span>Any time Change Subscription</span>
                        </div>
                        <div className='feature-box glass'>
                            <img src={SecureIcon} alt='' />
                            <span>Pay Bills In App Secuirly</span>
                        </div>
                        <div className='feature-box glass'>
                            <img src={CustomerIcon} alt='' />
                            <span>24 x 7 Customer Support</span>
                        </div>
                    </div>
                    <div className='call-to-action-form glass'>
                        <h1 style={{ 'textAlign': 'center' }}>Get In Touch</h1>
                        <form>
                            <div className='field-container'>
                                <img src={UserIcon} alt='' />
                                <input type='text' placeholder='Phone Number' />
                            </div>
                            <center>
                                <textarea placeholder='Your Message'></textarea>
                                <input type='submit' value='submit' className='button submit-btn' />
                            </center>
                        </form>
                    </div>
                </div>
                <div className='footer glass'>
                    <p> @ All rights are reserved by News Wala</p>
                </div>
            </div>
        </span>
    )
}
export default Home;