import React,{useState} from 'react';
import '../../styles.css';
import axios from 'axios';
const Settings = props =>{
    const [name,setName] = useState('');
    const handleUserNameUpdate = (e)=>{
        e.preventDefault();
        const name = document.getElementById('username')
        const btn = document.getElementById("u-submit");
        btn.value = "Loading.."
        axios.post('/customer/change-username/',{'name':name.value},{ headers: {Authorization: 'Bearer ' + window.localStorage.getItem('token')}})
        .then(resp=>{
            btn.value = "Updated succesfully"
            window.setTimeout(()=>{
                btn.value = "update"
                setName(resp.data)
            },2000)
        })
        .catch(errors=>{
            console.log(errors)
        })
    }
    const handlepasswordUpdate = (e)=>{
        e.preventDefault();
        const password1 = document.getElementById('password1')
        const password2 = document.getElementById('password2')
        const old = document.getElementById('old')
        const btn = document.getElementById('update-password')
        btn.value="loading..."
        if(password1.value !== password2.value){
            btn.value="Password Not match"
            window.setTimeout(()=>btn.value = "update",2000);
            // password1.focus;
            // password2.focus;
            password1.value='';
            password2.value='';
        }
        else{
            axios.post('/customer/change-password/',{'old':old.value,'new':password1.value},{ headers: {Authorization: 'Bearer ' + window.localStorage.getItem('token')}})
            .then(resp=>{
                btn.value="Password Changed"
                window.setTimeout(()=>{
                    btn.value="update";
                },2000);
                
            })
            .catch(errors=>{
                console.log(errors)
                btn.value="Wrong old password"
                window.setTimeout(()=>{
                    btn.value="update";
                },2000);
            })
        }
    }
    return (
        <div>
            <div className='user-name-field'>
                <p><span style={{'color':'grey'}}>Current : </span> {name === '' ? props.info.name : name}</p>
                <center><form onSubmit={(e)=>handleUserNameUpdate(e)}> 
                    <input type='text' placeholder='New UserName' id='username' required/>
                    <input type='submit' value='update' className='submit-btn' id="u-submit"/>
                </form></center>
            </div>
            <div className='password-field'>
                <h3>Update Password</h3>
                <center><form onSubmit={(e)=>handlepasswordUpdate(e)}>
                    <input type='password' placeholder='Old Password' id ="old" required/>
                    <input type='password' placeholder='New Password' id ="password1" required/>
                    <input type='password' placeholder='Confirm Password' id="password2" required/>
                    <input type='submit' value='update' className='submit-btn' id="update-password"/>
                </form></center>
            </div>
        </div>
    )
}

export default Settings;