import React,{useEffect, useState} from 'react';
import './styles.css';
import axios from 'axios';
const AddModal = (props) =>{
    const [data,setData] = useState('')
    useEffect(()=>{
        axios.get('/customer/papers/',{ headers: {Authorization: 'Bearer ' + window.localStorage.getItem('token')}})
        .then((resp)=>{
            setData(resp.data)
        })
        .catch((errors)=>console.log(errors))
    },[])
    const addToSub = (id)=>{
        axios.post('/customer/subscription/',{'id':id},{ headers: {Authorization: 'Bearer ' + window.localStorage.getItem('token')}})
        .then((resp)=>{
            console.log(resp)
            props.successfullFunc()
        })
        .catch((resp)=>{
            console.log(resp)
        })
    }
    const renderData = ()=>{
        return (
            <span>
                {data.length >0 ? <div className='subscription-data'>
                    {data.map((dataItem)=>{
                    return (
                        <div key={dataItem.id} className='image-container'>
                            <img src={`${dataItem.image}`} alt='' />
                            <div className='imge-data'>
                                <span>
                                    <span className='title'>{dataItem.title}</span>
                                    <span className='vertical-divider'>|</span>
                                    <span className='price'>&#8377; {dataItem.price}</span>
                                </span>
                                <button onClick={()=>{
                                    addToSub(dataItem.id)
                                }} style={{'backgroundColor':'rgb(37, 37, 255)'}}>Add</button>
                            </div>
                        </div>
                    )
                })}
                </div>: <p style={{'color':'rgba(0,0,0,0.7)','textAlign':'center','fontSize':'1.1rem'}}>No Papers to Add</p>}
            </span>
        )
    }
    return (
        <div>
            <div className='backdrop' onClick={()=>props.closeModal()}></div>
                <div className='modal-content big'>
                    <span className='back-btn' onClick={()=>props.closeModal()}>
                        x
                    </span>
                    <div className='modal-data'>
                        {data === '' ? <div>Loading...</div> : renderData()}
                    </div>
            </div>
        </div>
    )
}
export default AddModal;