import React from 'react';
import {Route,Switch} from 'react-router-dom';
import Dashborad from './components/Dashboard';
import CustomerDashboard from './components/customer/dashboard'
import PaperBoyDashboard from './components/paperboy/Dashboard'
import ManagerDashboard from './components/manager/dashboard';
import AccountantDashboard from './components/accountant/Dashboard';
import AdminDashboard from './components/admin/Dashboard'
import SignuP from './components/Signup'
// import {connect} from 'react-redux'
import Home from './components/Home'
const App = props =>{
  return (
    <Switch>
      <Route path='/' exact >
        <Home />
      </Route>
      <Route path='/dashboard/:user_type' exact>
        <Dashborad />
      </Route>
      <Route path='/customer' exact>
        <CustomerDashboard />
      </Route>
      <Route path='/paperboy' exact>
        <PaperBoyDashboard />
      </Route>
      <Route path='/manager' exact>
        <ManagerDashboard />
      </Route>
      <Route path='/accountant' exact>
        <AccountantDashboard />                                                                                 
      </Route>
      <Route path='/admin' exact>
        <AdminDashboard />
      </Route>
      <Route path='/signup' exact >
        <SignuP />
      </Route>
    </Switch>
  )
}
export default App;
