import React from 'react';

const adressForm = (props) =>{
    const handleFormData = ()=>{
        const doorno        = document.getElementById('doorno');
        const colony        = document.getElementById('colony');
        const landmark      = document.getElementById('landmark');
        const village       = document.getElementById('village');
        const mandal        = document.getElementById('mandal');
        const pincode       = document.getElementById('pincode');
        if (doorno.value === ''){
            doorno.focus();
        }
        else if (colony.value === ''){
            colony.focus();
        }
        else if(landmark.value === '' ){
            landmark.focus();
        }
        else if (village.value === ''){
            village.focus();
        }
        else if(mandal.value === '' ){
            mandal.focus();
        }
        else if(pincode.value === '' ){
            pincode.focus();
        }
        else{
            const data ={
                doorno:doorno.value,
                colony:colony.value,
                landmark:landmark.value,
                village:village.value,
                mandal:mandal.value,
                pincode:pincode.value
            }
            props.receiveData(data)
        }
    }
    return (
        <div className='form-content'>
            <form>
                <input type='text' placeholder='Door No' id='doorno'/>
                <input type='text' placeholder='colony name' id='colony'/>
                <input type='text' placeholder='Your landmarak' id='landmark'/>
                <input type='text' placeholder='village name' id='village'/>
                <input type='text' placeholder='mandal name' id='mandal'/>
                <input type='text' placeholder='Your pincode' id='pincode'/>
            </form>
            <div className='form-content-buttons'>
                <button className='disable'><span>&#8592;</span> Previous </button>
                <button onClick={()=>handleFormData()}> Next <span>&#8594;</span></button>
            </div>
        </div>
    )
}
export default adressForm;